.Timer {
  background-color: red;
  cursor: pointer;
  width: 100%;
  height: 100%;
}

.Timer-clock {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 6em;
  font-weight: lighter;
  color: hsl(5, 50%, 35%);
  z-index: 1;
}

.flex-container {
  background-color: tomato;
  padding: 0;
  margin: 0;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  width: 100%;
  height: 100%;
}

.flex-item {
  background-color: lightgray;
  height: 10%;
  width: 10%;
}



