.AppHeader {
  background-color: hsl(5, 50%, 35%);
  padding: 1em;
  color: white;
  text-align: center;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 7em;
}

.AppName {
  display: inline;
  margin: 1em;
  float: left;
}

.Input {
  margin: 1em;
}

label {
  display: block;
}

.break-slider {
  float: right;
}

.session-slider {
  float: right;
}

button {
  display: block;
  float: right;
  outline: 0;
  color: white;
  background-color: hsl(5, 60%, 35%);
  border: 0.2em solid hsl(5, 50%, 35%);
  border-radius: 20%;
  width: 5em;
  height: 3em;
  -webkit-box-shadow: 0 0 0.2em black;
          box-shadow: 0 0 0.2em black;
  text-align: center;
  font-size: 1em;
  font-weight: bold;
}

button:focus {
  outline: 0;
}

button:hover {
  background-color: hsl(5, 60%, 45%);
}

button:active {
  border: 0.2em solid hsl(5, 50%, 35%);
  -webkit-box-shadow: 0 0 0.2em hsl(5, 80%, 65%);
          box-shadow: 0 0 0.2em hsl(5, 80%, 65%);
}




